import React from 'react'

export default function Challanges_sec() {
  return (
   <section className='challanges-sec container'>
        <h2><span>CHALLENGES</span> of Implementing EQ/ SEL</h2>

        <div className='challanges-sec-cards-div'>
            <div className='challanges-sec-cards-body'>
                <img src='https://learn2strut.com/wp-content/uploads/2021/08/Rectangle-2.png' />
                <h3>No Time</h3>
                <p>Regular EQ/ SEL programs add four more hours a week for teachers and are difficult to fit in tight school schedules.</p>
            </div>
            <div className='challanges-sec-cards-body'>
                <img src='https://learn2strut.com/wp-content/uploads/2021/08/Rectangle-3.png' />
                <h3>No Money</h3>
                <p>CASEL states that EQ/ SEL implementation costs between $700–$1000 per student, which goes beyond school budgets.</p>
            </div>
            <div className='challanges-sec-cards-body'>
                <img src='https://learn2strut.com/wp-content/uploads/2021/08/Rectangle-4.png' />
                <h3>No Help</h3>
                <p>Lack of digital access to EQ/ SEL programs and difficulty with combining EQ/ SEL and academic lessons are big challenges.</p>
            </div>
        </div>
   </section>
  )
}
