import React,{useState} from "react";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import ArrowCircleLeftOutlinedIcon from "@mui/icons-material/ArrowCircleLeftOutlined";
import ArrowCircleRightOutlinedIcon from "@mui/icons-material/ArrowCircleRightOutlined";

export default function Partner_Curosel() {
  const renderNextButton = ({ isDisabled }) => {
    return (
      <ArrowCircleRightOutlinedIcon
        fontSize="large"
        style={{ position: "absolute", right:-50, top: 30, cursor: "pointer" }}
      />
    );
  };
  const renderPrevButton = ({ isDisabled }) => {
    return (
      <ArrowCircleLeftOutlinedIcon
        fontSize="large"
        style={{ position: "absolute", left: -50, top: 30, cursor: "pointer" }}
      />
    );
  };

  const handleDragStart = (e) => e.preventDefault();
  const responsive = {
    2000: {
      items: 11,
    },
    1200: {
      items: 4,
    },
    800: {
      items: 3,
    },
    0: {
      items: 2,
    },
  };
  const items = [
    <img src="https://learn2strut.com/wp-content/uploads/2021/09/Design-for-ALLCons-1-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/distinctive-3-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/client-3-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/gBeta-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/v-logo-orange-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/midwest-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/Recovered-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/Recovered2-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/Schuler-Logo-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/site-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/Smart-Course-1-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/New-Project-10-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/client-4-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/touchgiftfoundation-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/USCRossier-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/asel-1.png"></img>,
  <img src="https://learn2strut.com/wp-content/uploads/2021/09/client-1-copy-1.png"></img>,
  ];
   function disableButton(){ 
    return window.innerWidth<=990?true:false  
}
   const [screen ,setScreen]=useState( disableButton);
    window.onresize =()=>{window.innerWidth <=990 ?setScreen(true):setScreen(false)};
     
  return (
    
    <section className='partner-curosel container'>
    
<p className='curosel-title-p'>Our Valued Partners</p>


<AliceCarousel
          mouseTracking
          items={items}
          responsive={responsive}
          autoPlay={true}
          infinite={true}
          animationDuration={800}
          autoPlayInterval={2000}
          renderPrevButton={renderPrevButton}
          renderNextButton={renderNextButton}
          disableButtonsControls={screen}
          
        /> 


<h2>Choose Your <span>PROFILE</span></h2>
<div className='partner-curosel-card-div'>
  <div className='partner-curosel-card-body'>
  <h3>Principals</h3>
  <p>Promote teacher satisfaction and save money through our preloaded EQ/ SEL curricula</p>
  <a href='#'><button className='blue-btn curosel-card-btn'>Learn More</button></a>
  </div>

  <div className='partner-curosel-card-body'>
    <h3>Teachers</h3>
    <p>Find adaptive technology that saves time through personalized learning that improves grades</p>
    <a href='#'><button className='blue-btn curosel-card-btn'>Learn More</button></a>
  </div>

  <div className='partner-curosel-card-body'>
    <h3>Parents</h3>
    <p>Learn how our preloaded EQ/ curricula can improve your grades and social relationships</p>
    <a href='#'><button className='blue-btn curosel-card-btn'>Learn More</button></a>
  </div>

  <div className='partner-curosel-card-body'>
    <h3>Corporate</h3>
    <p>Increase performance and develop better organizational culture with our adaptive technology</p>
    <a href='#'><button className='blue-btn curosel-card-btn'>Learn More</button></a>
  </div>
</div>
    </section>
  )
}

















