import React,{useState} from "react";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import ArrowCircleLeftOutlinedIcon from "@mui/icons-material/ArrowCircleLeftOutlined";
import ArrowCircleRightOutlinedIcon from "@mui/icons-material/ArrowCircleRightOutlined";
import Why_Strut_Cards from "./Why_Strut-comp/Why_Strut_Cards";

export default function Why_Strut() {
  const renderNextButton = ({ isDisabled }) => {
    return (
      <ArrowCircleRightOutlinedIcon
        fontSize="large"
        style={{ position: "absolute", right:-50, top: 120, cursor: "pointer" }}
      />
    );
  };
  const renderPrevButton = ({ isDisabled }) => {
    return (
      <ArrowCircleLeftOutlinedIcon
        fontSize="large"
        style={{ position: "absolute", left: -50, top: 120, cursor: "pointer" }}
      />
    );
  };

  const handleDragStart = (e) => e.preventDefault();
  const responsive = {
    2000: {
      items: 11,
    },
    1200: {
      items: 3,
    },
    800: {
      items: 2,
    },
    0: {
      items: 1,
    },
  };
  const items = [
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/1516510921758-modified.png" name="CHRIS G." detail="STRUT is dedicated to creating a vehicle for promoting social and emotional learning (SEL) in..."  />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/Marisa-Pfeiffer-modified.png" name="Marissa P." detail="The STRUT team is a supportive and selfless team that is passionate about developing impactful..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/09/Boesch-J-modified.png" name="Boesch J." detail="I learned how to become more culturally aware of my students and ways in which..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/09/gray-avatar.png" name="Nicole R." detail="I believe it is vital for kids and clients to be successful both in and..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/1606197658722-modified.png" name="Atzallali S." detail="STRUT is an amazing, personal company where all of the hard-working members are working towards..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/1592229964856-modified.png" name="Ella Z." detail="STRUT helps everyone become lifelong learners and reflectors of their emotions. I had almost everyone..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/1579997446113-modified.png" name="Allison M." detail="STRUT helps everyone become lifelong learners and reflectors of their emotions. I had almost everyone..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/1548362067387-modified.png  " name="Remington I." detail="STRUT’s products are designed to support student needs at all levels! Whether it’s student curriculum..." />,
    <Why_Strut_Cards src="https://learn2strut.com/wp-content/uploads/2021/10/Remington-modified.png" name="Remington I." detail="I have always felt strongly about developing my students’ SEL skills, in hopes that they..." />,
  ];
   function disableButton(){ 
    return window.innerWidth<=990?true:false  
}
   const [screen ,setScreen]=useState( disableButton);
    window.onresize =()=>{window.innerWidth <=990 ?setScreen(true):setScreen(false)};
  return (
    <section className="why-strut-sec container">
      <h2>Why <span>STRUT Learning</span>?</h2>

      <div>
        <AliceCarousel
          mouseTracking
          items={items}
          responsive={responsive}
          autoPlay={true}
          infinite={true}
          animationDuration={800}
          autoPlayInterval={2000}
          renderPrevButton={renderPrevButton}
          renderNextButton={renderNextButton}
          disableButtonsControls={screen}
          
        />   
      </div>
    </section>
  );
}

