import React from 'react';

export default function Why_Strut_Cards(props) {
  return (
    <div className='why-strut-card-div'>
    <div className='why-strut-card-body'>
        <img src={props.src} />
        <h3>{props.name}</h3>
        <p>{props.detail}</p>
    </div>
    </div>
  )
}
