import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Offcanvas from 'react-bootstrap/Offcanvas';

export default function Header() {
  return (
   <header className='header'>
   <div className='hthb-notification'>
<p>
    <strong>
    Register for our <a href=''>EQ/ SEL Summit </a>Today!
    </strong>
</p>
   </div>
 {['lg'].map((expand) => (
        <Navbar key={expand} bg="white" expand={expand} className="mb-3 head-navbar">
          <Container >
            <Navbar.Brand href="#"><img className='nav-logo' src={require("../assests/strut_logo.png")}></img></Navbar.Brand>
            <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-${expand}`} />
            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-${expand}`}
              aria-labelledby={`offcanvasNavbarLabel-expand-${expand}`}
              placement="end"
            >
              <Offcanvas.Header closeButton>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav className="justify-content-between flex-grow-1 pe-3">
                  <Nav className='head-nav-item'> <Nav.Link  href="#action1">WHAT IS EQ/ SEL?</Nav.Link></Nav>
                  <NavDropdown className='head-nav-item'
                    title="ABOUT"
                    id={`offcanvasNavbarDropdown-expand-${expand}`}
                  >
                    <NavDropdown.Item href="#action3">Who We Are</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Our Team</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action5">Our Partners</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action5">Our Community</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action5">First 100</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action5">Support Us</NavDropdown.Item>
                  </NavDropdown>
                  <NavDropdown className='head-nav-item'
                    title="PRODUCTS"
                    id={`offcanvasNavbarDropdown-expand-${expand}`}
                  >
                    <NavDropdown.Item href="#action3">EQ360</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">eMojo</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Demo</NavDropdown.Item>
                  </NavDropdown>
                  <NavDropdown className='head-nav-item'
                    title="RESOURCE"
                    id={`offcanvasNavbarDropdown-expand-${expand}`}
                  >
                    <NavDropdown.Item href="#action3">Daily EQ/ SEL</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">EQ/ SEL Activities</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Flyers</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Blog</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">EQ2A Summit</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Virtual Roundtables</NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action4">Publications</NavDropdown.Item>
                    
                   
                  </NavDropdown>
                  <Nav className='head-nav-item'>
                  <Nav.Link href="#action1" className='head-nav-item'>CONTACT US</Nav.Link>
                  </Nav>
                  <Nav.Link className='join-nav-item' href="#action1">JOIN THE MOVEMENT</Nav.Link>
                  
                </Nav>
              
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
      ))}

   </header>
  )
}




